* {
  box-sizing: border-box;
}

textarea, input {
  font-family: inherit;
  font-size: inherit;
}

/****** Main Menu *******/

#MainMenu {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#SignInDialouge {
  display: grid;
  grid-template-columns: repeat(2, auto);
}

/****** Search Bar *******/

#SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
}

#SearchBar>form{
  max-width: 400px;
  display: grid;
  grid-template-columns: repeat(2, auto);
}

/******* Vocab Table *******/

#VocabTable {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 20px;
}

/* Vocab Header*/

#VocabHeader {
  display: flex;
  justify-content: center;
  position: sticky;
  top: 0;
  background-color: white;
}

#VocabHeader>div {
  width: 100px;
}

#VocabHeader>#english, #VocabHeader>#notes {
  width: 210px;
}

#VocabHeader>#entryBtns {
  width: 84px;
}

/* Vocab Entry */

.VocabEntryWrapper {
  display: flex;
  justify-content: center;
}

.VocabEntry {
  display: contents;
}

.VocabEntry>*:disabled {
  color: black;
}

.VocabEntry>* {
  width: 100px;
}

.VocabEntry>textarea {
  border: none;
  background-image: none;
  background-color: transparent;
  box-shadow: none;
  resize: none;
}

.VocabEntry>textarea[name="englishString"] {
  width: 210px;
}

.VocabEntry>*[name="notes"] {
  border: none;
  background-image: none;
  background-color: transparent;
  box-shadow: none;
  width: 210px;
}

.entryBtns {
  display: flex;
  padding: 5px;
}

.entryBtns button {
  all: unset;
  margin: 5px;
  cursor: pointer;
}

.entryBtns button:disabled {
  opacity: 0.7;
  color: grey;
}

/******* Notes Input *******/

.NotesInput {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(90, 90, 90, 0.5);
  z-index: 9999;
}

.NotesInput div {
  display: flex;
  flex-direction: column;
}